<template>
    <div class="" id="sideBox" style="width: 200px;">
        <el-menu default-active="2" unique-opened class="el-menu-vertical-demo" style="border-right: none;background-color:#545c6200 !important ;" @select='select'  background-color="#545c6200 " text-color="#ff0000" active-text-color="#ffd04b">
            <el-menu-item index="index">
                <span class="el-icon-house " :class='[isClass=="index"?"home1":""]'></span>
                <span slot="title">首页</span>
            </el-menu-item>
            <el-submenu index="1" StaticEnableDefaultPopOutImage='false'>
                <template slot="title" >
                    <span class="el-icon-view" :class='[isClass=="count"?"eyes":""]'></span>
                    <span slot="title">view</span>
                </template>
                <el-menu-item-group>
                    <!-- <template slot="title">分组一</template> -->
                    <el-menu-item index="count">countChart</el-menu-item>
                    <!-- <el-menu-item index="1-2">选项2</el-menu-item> -->
                </el-menu-item-group>
            </el-submenu>
            <el-submenu index="2" StaticEnableDefaultPopOutImage='false'>
                <template slot="title" >
                    <span class="el-icon-view" :class='[isClass=="count"?"eyes":""]'></span>
                    <span slot="title">three</span>
                </template>
                <el-menu-item-group>
                    <!-- <template slot="title">分组一</template> -->
                    <el-menu-item index="three">three</el-menu-item>
                    <el-menu-item index="pointerLockControls">pointer</el-menu-item>
                    <el-menu-item index="curve">curve</el-menu-item>
                    <el-menu-item index="PerspectiveCapture">PerspectiveCapture</el-menu-item>
                    <!-- <el-menu-item index="1-2">选项2</el-menu-item> -->
                </el-menu-item-group>
            </el-submenu>

            <el-submenu index="5">
                <template slot="title">
                    <span class="el-icon-sunny" :class='[isClass=="5"||isClass=="globla"?"home0":""]'></span>
                    <span>animation</span>
                </template>
                <el-menu-item-group>
                    <!-- <template slot="title">分组1</template> -->
                    <el-menu-item index="globla">3d视觉欺诈</el-menu-item>
                    <!-- <el-menu-item index="5-2">选项2</el-menu-item> -->
                </el-menu-item-group>
                <!-- <el-menu-item-group title="分组2">
                    <el-menu-item index="5-3">选项3</el-menu-item>
                </el-menu-item-group>
                <el-submenu index="5-4">
                    <template slot="title">选项4</template>
                    <el-menu-item index="5-4-1">选项1</el-menu-item>
                </el-submenu> -->
            </el-submenu>
            <el-menu-item index="prict">
                <span class="el-icon-menu" :class='[isClass=="prict"?"home0":""]'></span>
                <span slot="title">发表</span>
            </el-menu-item>
            <el-menu-item index="login">
                <span class="el-icon-document " :class='[isClass=="login"?"login":""]'></span>
                <span slot="title">登陆</span>
            </el-menu-item>
        </el-menu>
    </div>
</template>
<script>
    // import  {Menu,MenuItem} from 'element-ui'
    export default {
        name: '',
        // components:{
        //     'Menu':el-menu,'MenuItem':el-menu-item
        // },
        data() {
            return {
                screenWidth:0,
                isClass:''
            }
        },
        mounted() {
          console.log(this.getViewportSize)
        },
        methods: {
            select(key, keyPath){
                console.log(key)
                // if(this.$store.state.isLogin){
                    this.$router.push({name:key})
                    this.isClass=key
                // }
            }
        },
    }
</script>

<style lang="less">
    .el-icon-arrow-down:before{content: none !important; display: none !important;color: transparent!important;}
    .el-menu-item:hover{
        background-color: #ff00001a !important;
    }
    .el-submenu__title:hover{
        background-color: #ff00001a !important;
    }
    .is-active{
        background-color: #545c6200 !important;
    }
</style>

<style scoped lang='less'>
    .home0{
        animation: home 5s cubic-bezier(1,1,1,1) 0s infinite;
    }
    .home1{
        animation: home1 2s cubic-bezier(1,1,1,1) 0s infinite;
    }
    .eyes{
        animation: view 2s cubic-bezier(1,1,1,1) 0s infinite;
    }
    .login{
        animation: login 1s cubic-bezier(1,1,1,1) 0s infinite;
    }
    @keyframes home {
        0%{
            transform: rotateZ(0deg)
        }
        100%{
            transform: rotateZ(360deg)
        }
    }
    @keyframes login {
        0%{
            transform: rotateY(0deg)
        }
        100%{
            transform: rotateY(360deg)
        }
    }
    @keyframes view {
        0%{
             opacity: 1;
        }
        50%{
             opacity: 0;
        }
        100%{
             opacity: 1;
        }
    }
    @keyframes home1 {
        0%{
             transform: rotateZ(0deg);
        }
        25%{
            transform: rotateZ(45deg);
        }
        50%{
            transform: rotateZ(0deg);
        }
        75%{
            transform: rotateZ(-45deg);
        }
        100%{
            transform: rotateZ(0deg);
        }
    }
</style>



